/* PLUGIN STYLESHEET */

// lightSlider

.lSSlideOuter {
	overflow: hidden;
	-webkit-touch-callout: none;
	-webkit-user-select: none;
	-khtml-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
}
.lightSlider {
	overflow: hidden;

	&:before, &:after {
		content: " ";
		display: table;
	}
}
.lSSlideWrapper {
	max-width: 100%;
	overflow: hidden;
	position: relative;

	> .lightSlider:after {
		clear: both;
	}

	.lSSlide {
		-webkit-transform: translate(0px, 0px);
		-ms-transform: translate(0px, 0px);
		transform: translate(0px, 0px);
		-webkit-transition: all 1s;
		-webkit-transition-property: -webkit-transform,height;
		-moz-transition-property: -moz-transform,height;
		transition-property: transform,height;
		-webkit-transition-duration: inherit;
		transition-duration: inherit;
		-webkit-transition-timing-function: inherit;
		transition-timing-function: inherit;
	}

	.lSFade {
		position: relative;

		li {
			position: absolute !important;
			top: 0;
			left: 0;
			z-index: 9;
			margin-right: 0;
			width: 100%;
			height: 100%;

			&.active {
				z-index: 10;
			}
		}
	}
}
.lSSlideWrapper.usingCss .lSFade > * {
	opacity: 0;
	-webkit-transition-delay: 0s;
	transition-delay: 0s;
	-webkit-transition-duration: inherit;
	transition-duration: inherit;
	-webkit-transition-property: opacity;
	transition-property: opacity;
	-webkit-transition-timing-function: inherit;
	transition-timing-function: inherit;

	&.active {
		opacity: 1;
	}
}

.lSSlideOuter {

	.lSPager.lSpg {
		margin: 10px 0 0;
		padding: 0;
		text-align: center;

		> li {
			cursor: pointer;
			display: inline-block;
			padding: 0 5px;

			a {
				background-color: #222222;
				border-radius: 30px;
				display: inline-block;
				height: 8px;
				overflow: hidden;
				text-indent: -999em;
				width: 8px;
				position: relative;
				z-index: 99;
				-webkit-transition: all 0.5s linear 0s;
				transition: all 0.5s linear 0s;
			}

			&:hover a,
			&.active a {
				background-color: #428bca;
			}
		}
	}

	.lSPager.lSGallery {
		list-style: none outside none;
		padding-left: 0;
		margin: 0;
		overflow: hidden;
		transform: translate3d(0px, 0px, 0px);
		-moz-transform: translate3d(0px, 0px, 0px);
		-ms-transform: translate3d(0px, 0px, 0px);
		-webkit-transform: translate3d(0px, 0px, 0px);
		-o-transform: translate3d(0px, 0px, 0px);
		-webkit-transition-property: -webkit-transform;
		-moz-transition-property: -moz-transform;
		-webkit-touch-callout: none;
		-webkit-user-select: none;
		-khtml-user-select: none;
		-moz-user-select: none;
		-ms-user-select: none;
		user-select: none;

		&:before,
		&:after {
			content: " ";
			display: table;
		}
		&:after {
			clear: both;
		}

		li {
			opacity: 0.7;
			overflow: hidden;
			-webkit-transition: opacity 0.35s linear 0s;
			transition: opacity 0.35s linear 0s;

			&:hover,
			&.active {
				opacity: 1;
			}
		}

		img {
			display: block;
			height: auto;
			max-width: 100%;
		}
	}

	.media {
		opacity: 0.8;

		&.active { opacity: 1; }
	}

	&.vertical {
		position: relative;

		&.noPager { padding-right: 0px !important; }

		.lSGallery {
			position: absolute !important;
			right: 0;
			top: 0;
		}

		.lightSlider > li {
			width: 100% !important;
			max-width: none !important;
		}
	}
}

.lSAction {

	> a {
		width: 32px;
		display: block;
		top: 50%;
		height: 32px;
		background-image: url('../assets/img/icons/lslider-controls.png');
		cursor: pointer;
		position: absolute;
		z-index: 9990;
		margin-top: -16px;
		opacity: 0.5;
		-webkit-transition: opacity 0.35s linear 0s;
		transition: opacity 0.35s linear 0s;

		&:hover { opacity: 1; }

		&.disabled { pointer-events: none; }
	}

	> .lSPrev {
		background-position: 0 0;
		left: 10px;
	}

	> .lSNext {
		background-position: -32px 0;
		right: 10px;
	}
}
.cS-hidden {
	height: 1px;
	opacity: 0;
	filter: alpha(opacity=0);
	overflow: hidden;
}

.lSSlideOuter.lSrtl {
	direction: rtl;
}
.lSSlideOuter .lightSlider, .lSSlideOuter .lSPager {
	padding-left: 0;
	list-style: none outside none;
}
.lSSlideOuter.lSrtl .lightSlider, .lSSlideOuter.lSrtl .lSPager {
	padding-right: 0;
}
.lSSlideOuter .lightSlider li,  .lSSlideOuter .lSGallery li {
	float: left;
}
.lSSlideOuter.lSrtl .lightSlider li,  .lSSlideOuter.lSrtl .lSGallery li {
	float: right !important;
}

@-webkit-keyframes rightEnd {
	0% { left: 0; }
	50% { left: -15px; }
	100% { left: 0; }
}
@keyframes rightEnd {
	0% { left: 0; }
	50% { left: -15px; }
	100% { left: 0; }
}
@-webkit-keyframes topEnd {
	0% { top: 0; }
	50% { top: -15px; }
	100% { top: 0; }
}
@keyframes topEnd {
	0% { top: 0; }
	50% { top: -15px; }
	100% { top: 0; }
}
@-webkit-keyframes leftEnd {
	0% { left: 0; }
	50% { left: 15px; }
	100% { left: 0; }
}
@keyframes leftEnd {
	0% { left: 0; }
	50% { left: 15px; }
	100% { left: 0; }
}
@-webkit-keyframes bottomEnd {
	0% { bottom: 0; }
	50% { bottom: -15px; }
	100% { bottom: 0; }
}
@keyframes bottomEnd {
	0% { bottom: 0; }
	50% { bottom: -15px; }
	100% { bottom: 0; }
}
.lSSlideOuter .rightEnd {
	-webkit-animation: rightEnd 0.3s;
	animation: rightEnd 0.3s;
	position: relative;
}
.lSSlideOuter .leftEnd {
	-webkit-animation: leftEnd 0.3s;
	animation: leftEnd 0.3s;
	position: relative;
}
.lSSlideOuter.vertical .rightEnd {
	-webkit-animation: topEnd 0.3s;
	animation: topEnd 0.3s;
	position: relative;
}
.lSSlideOuter.vertical .leftEnd {
	-webkit-animation: bottomEnd 0.3s;
	animation: bottomEnd 0.3s;
	position: relative;
}
.lSSlideOuter.lSrtl .rightEnd {
	-webkit-animation: leftEnd 0.3s;
	animation: leftEnd 0.3s;
	position: relative;
}
.lSSlideOuter.lSrtl .leftEnd {
	-webkit-animation: rightEnd 0.3s;
	animation: rightEnd 0.3s;
	position: relative;
}