
/* - INPUT STYLESHEET - */

$btn-height: 34px;

.input-group-btn .btn {
	height: $btn-height;
}

// GLOBAL

// Inputs
input,
.form-control {
	border-color: $input-bd;
	background-color: $input-bg;
	box-shadow: none;
	transition: border 0.2s, background 0.2s, box-shadow 0.2s;
}

// Selects
select {
	padding: 6px;
	font-size: $font-size-s;
	border-radius: 2px;
	background-color: #f8f8f8;
	border: 1px solid rgba(0,0,0,0.1);
	box-shadow: 0 1px 0 rgba(0,0,0,0.1);
}


// INPUT STATES

// Input & Button Focus Style
input:focus,
.form-control:focus {
	border-color: $input-focus-bd;
	box-shadow: 0 0 3px $input-focus-bs;
}

// Override input & button outline
input,
button,
select,
.button,
.btn:focus,
.btn.focus,
.btn:active:focus,
.btn:active.focus,
.btn.active:focus,
.btn.active.focus {
	outline: none !important;
}


// Input Styling For Aqua Theme
.theme-aqua {
	input,
	.form-control,
	select {
		border-color: $aqua-border;
	}
}

// INPUT THEMES

%input-dark {
	color: $text-light-color;
	border-color: $input-bd-dark;
	background-color: $input-bg-dark;
}

%button-dark {
	color: $text-light-color;
	border-color: $input-bd-dark;
	background-color: $btn-bg-dark;
}