
/* - MIXT GLOBAL STYLING - */

// PAGE LOADER

body.loading {
	overflow: hidden;
	height: 100%;
	max-height: 100%;
}

#load-overlay {
	position: fixed;
	top: 0; left: 0;
	right: 0; bottom: 0;
	width: 100%;
	height: 100%;
	display: table;
	opacity: 0;
	visibility: hidden;
	background: $theme-accent;
	z-index: 999999;

	transition: visibility 0.2s ease 0.2s, opacity 0.2s ease 0.2s;

	.load-inner {
		display: table-cell;
		max-width: 300px;
		max-height: 300px;
		text-align: center;
		vertical-align: middle;

		img {
		}

		img,
		div {
			margin: 0 auto;
			max-width: 300px;
			max-height: 300px;
		}
	}

	// SHAPES

	.circle {
		border-radius: 32px;
	}

	.circle, .square {
		width: 36px;
		height: 36px;
		background-color: #fff;
	}

	.ring {
		border-radius: 32px;
	}

	.ring, .square2 {
		width: 32px;
		height: 32px;
		border: 4px solid #fff;
	}
}

.loading #load-overlay,
.loading-out #load-overlay {
	opacity: 1;
	visibility: visible;
}
.loading #load-overlay {
	transition: visibility 0s, opacity 0s;
}

.no-visibility #load-overlay {
	display: none;
}
.no-visibility.loading #load-overlay,
.no-visibility.loading-out #load-overlay {
	display: block;
}