// MIXT CUSTOM ANIMATIONS

.animated.pulsate {
	opacity: 0;
	animation: pulsate 0.8s ease;
	animation-iteration-count: infinite;
}

@keyframes pulsate {
	0% {
		transform: scale(0.8);
		opacity: 0.4;
	}
	50% {
		opacity: 1;
	}
	75% {
		opacity: 0.8;
	}
	100% {
		transform: scale(1.2);
		opacity: 0;
	}
}

