
/* - SIDEBAR STYLESHEET - */

.sidebar {

	// Headings / Widget Titles
	h3 {
		font-size: $font-size-l;
		margin-top: 0;
	}

	// Nav Menus
	.nav li {

		> ul,
		.menu-item-has-children > ul {
			border-top: 1px solid $aqua-border;
		}

		a {
			padding: $padding-s 0;
			&:hover { background: none !important; }
		}

		.post-date {
			display: block;
			font-size: $font-size-xs;
			margin-top: -$padding-s;
			padding-bottom: $padding-s;
		}

		.post-count {
			float: right;
		}

		li:last-child { border-bottom: 0; }
	}

	.nav li,
	#recentcomments li {
		border-bottom: 1px solid $aqua-border;
	}

	#recentcomments li { padding: $padding-s 0 !important; }

	// Widgets

	.widget {
		> select,
		.textwidget form > select {
			width: 100%;
		}
	}
}

.sidebar-padder {
	padding-top: $padding-l;
}